import * as React from "react";

export interface AttachmentItem {
  id: string;
  name: string;
  size: number;
  status: string;
}

export interface AttachmentListProps {
  items: AttachmentItem[];
}

const AttachmentList = (props: AttachmentListProps) => {
  const { items } = props;
  const listItems = items.map((item, index) => (
    <tr key={index}>
      <td>{item.status}</td>
      <td>{item.id}</td>
      <td>{item.name}</td>
      <td>{item.size}</td>
    </tr>
  ));
  return (
    <div>
      <h4 className="ms-font-xl ms-fontWeight-semilight ms-fontColor-neutralPrimary">Attachments</h4>
      <table>
        <tr>
          <th>Status</th>
          <th>ID</th>
          <th>Name</th>
          <th>Size</th>
        </tr>
        {listItems}
      </table>
    </div>
  );
};

export default AttachmentList;
