/* eslint-disable no-undef */
import React, { FC, useEffect, useState } from "react";
import { DefaultButton } from "@fluentui/react";
import Header from "./Header";
import AttachmentList, { AttachmentItem } from "./AttachmentList";
import Progress from "./Progress";
import { FileUploader } from "react-drag-drop-files";

/* global require */
export interface AppProps {
  title: string;
  isOfficeInitialized: boolean;
}

interface AttachmentsChangedEvent {
  attachmentStatus: Office.MailboxEnums.AttachmentStatus;
  type: Office.EventType;
  attachmentDetails: {
    id: string;
    name: string;
    size: number;
    attachmentType: Office.MailboxEnums.AttachmentType;
  };
}

function escapeXml(unsafe: string): string {
  return unsafe.replace(/[<>&'"]/g, function (c) {
    switch (c) {
      case "<":
        return "&lt;";
      case ">":
        return "&gt;";
      case "&":
        return "&amp;";
      case "'":
        return "&apos;";
      case '"':
        return "&quot;";
    }
  });
}

const App: FC<AppProps> = (props) => {
  const { title, isOfficeInitialized } = props;
  const [message, setMessage] = useState("Welcome ...");
  const [validUser, setValidUser] = useState(false);
  const [files, setFiles] = useState(null);
  const [attachmentItems, setAttachmentItems] = useState<AttachmentItem[]>([]);

  const handleChange = (files) => {
    setFiles(files);
    var reader = new FileReader();
    reader.onload = function (e) {
      var fileResult = e.target.result as string;
      var base64FileContent = fileResult.slice(fileResult.indexOf(",") + 1);
      Office.context.mailbox.item.addFileAttachmentFromBase64Async(
        base64FileContent,
        files[0].name,
        { isInline: false },
        async (result) => {
          console.log(result);
        }
      );
    };

    reader.readAsDataURL(files[0]);
  };

  if (!isOfficeInitialized) {
    return (
      <Progress
        title={title}
        logo={require("./../../../assets/logo-filled.png")}
        message="Please sideload your addin to see app body."
      />
    );
  }

  const handleAttachmentChange = (eventArgs: AttachmentsChangedEvent) => {
    const items = [...attachmentItems];
    items.push({
      id: eventArgs.attachmentDetails.id,
      name: eventArgs.attachmentDetails.name,
      size: eventArgs.attachmentDetails.size,
      status: eventArgs.attachmentStatus,
    });

    setAttachmentItems(items);
  };

  const createRequest = (subject: string, to: Office.EmailAddressDetails[], body: string): string => {
    const mailTo = to
      .map((item) => "<t:Mailbox><t:EmailAddress>" + item.emailAddress + "</t:EmailAddress></t:Mailbox>")
      .join("");

    const request =
      '<soap:Envelope xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:m="http://schemas.microsoft.com/exchange/services/2006/messages" xmlns:t="http://schemas.microsoft.com/exchange/services/2006/types" xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">' +
      '  <soap:Header><t:RequestServerVersion Version="Exchange2010" /></soap:Header>' +
      "  <soap:Body>" +
      '    <m:CreateItem MessageDisposition="SendAndSaveCopy">' +
      '      <m:SavedItemFolderId><t:DistinguishedFolderId Id="sentitems" /></m:SavedItemFolderId>' +
      "      <m:Items>" +
      "        <t:Message>" +
      "          <t:Subject>" +
      subject +
      "</t:Subject>" +
      '          <t:Body BodyType="HTML"> ' +
      escapeXml(body) +
      "!</t:Body>" +
      "          <t:ToRecipients>" +
      mailTo +
      "          </t:ToRecipients>" +
      "        </t:Message>" +
      "      </m:Items>" +
      "    </m:CreateItem>" +
      "  </soap:Body>" +
      "</soap:Envelope>";
    return request;
  };
  const handleEmailSend = async () => {
    // Office.context.mailbox.item.saveAsync(async (result) => {
    //   if (result.status === Office.AsyncResultStatus.Failed) {
    //     console.log("Error: " + result.error.message);
    //   } else {
    //     console.log("Item saved");
    //     Office.context.mailbox.
    //     Office.context.mailbox.item.close();
    //   }
    // });
    var item = Office.context.mailbox.item;
    // get body
    item.body.getAsync(Office.CoercionType.Html, function (result) {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        item.to.getAsync((toResult) => {
          if (toResult.status === Office.AsyncResultStatus.Succeeded) {
            item.subject.getAsync((subResult) => {
              const request = createRequest(subResult.value, toResult.value, result.value);
              Office.context.mailbox.makeEwsRequestAsync(request, function (result) {
                console.log(result);
                if (result.status === Office.AsyncResultStatus.Succeeded) {
                  Office.context.mailbox.item.saveAsync(async (result) => {
                    if (result.status === Office.AsyncResultStatus.Failed) {
                      console.log("Error: " + result.error.message);
                    } else {
                      console.log("Item saved");
                      Office.context.mailbox.item.close();
                    }
                  });
                }
              });
            });
          }
        });
      }
    });
  };

  useEffect(() => {
    if (isOfficeInitialized) {
      // check if user is valid
      Office.context.mailbox.getUserIdentityTokenAsync(async (result) => {
        if (result.status === Office.AsyncResultStatus.Failed) {
          setMessage(result.error.message);
          console.log("Error: " + result.error);
          console.log(result.diagnostics);
          setValidUser(false);
          return;
        } else {
          console.log("User token: " + result.value);
          setMessage("Welcome " + Office.context.mailbox.userProfile.emailAddress);
          setValidUser(true);

          // load all atachments
          Office.context.mailbox.item?.getAttachmentsAsync(async (result) => {
            if (result.status === Office.AsyncResultStatus.Failed) {
              console.log("Error: " + result.error.message);
            } else {
              console.log("Attachments: " + result.value);
              const items = result.value.map((item) => ({
                id: item.id,
                name: item.name,
                size: item.size,
                status: "Loaded",
              }));
              setAttachmentItems(items);
            }
          });

          Office.context.mailbox.item?.addHandlerAsync(
            Office.EventType.AttachmentsChanged,
            handleAttachmentChange,
            (asyncResult) => {
              if (asyncResult.status === Office.AsyncResultStatus.Failed) {
                console.log("Error: " + asyncResult.error.message);
              } else {
                console.log("Handler successfully registered for event: " + Office.EventType.AttachmentsChanged);
              }
            }
          );
        }
      });
    }
  }, [isOfficeInitialized]);
  return (
    <>
      <div className="ms-welcome">
        <Header logo={require("./../../../assets/logo-filled.png")} title={title} message={message} />
      </div>
      <main className="ms-welcome__main">
        <div>
          <FileUploader multiple={true} handleChange={handleChange} name="file" />
          {files && <p>File name:{files[0].name}</p>}
        </div>
        <AttachmentList items={attachmentItems} />
        <DefaultButton secondaryText="Send Email" onClick={handleEmailSend} text="Send Email" />
      </main>
    </>
  );
};

export default App;
